@charset "utf-8";
*{
    padding: 0px;
    margin: 0px;
}
ul,li,ol{list-style: none;}
a{
    text-decoration: none;
    color: #333;
}
body{
    font-family: "맑은 고딕",arial, sans-serif;
    font-size:12px;
    color:#333;
}

.text_purple{
    color: #5f0080;
}

/* header */
header{
    width: 100%;
    height: 156px;
    box-shadow: 0px 0px 3px #333;
}
header .header_inner{
    width: 1050px;
    height: 100px;
    margin: 0px auto;
    position: relative;
}
header .header_inner h1{
    position: absolute;
    top: 70%;
    left: 0px;
    transform: translateY(-40%);
}
header .header_inner .logo_nav{
    position: absolute;
    top: 70%;
    left: 106px;
    transform: translateY(-50%);
}
header .header_inner .logo_nav ul{
    display: flex;
    justify-content: space-around;
}
header .header_inner .logo_nav ul li{
    padding-right: 5px;
    font-size: 18px;
    font-weight: 800;
}
header .header_inner .logo_nav ul li::after{
    content: "";
    display: inline-block;
    width: 1px;
    height: 16px;
    background-color: #ccc;
    margin: 0px 5px 0px 5px;
}
header .header_inner .logo_nav ul>li:first-child{
    color: #5f0080;
}
header .header_inner .logo_nav ul>li:last-child{
    color: #cccccc;
}
header .header_inner .logo_nav ul li:last-child::after{
    display: none;
}
header .header_inner .logo_nav ul li sup{
    color: #ff0000;
    font-weight: 700;
    line-height: 0px;
    font-size: 10px;
    vertical-align: super;
}
header .header_inner .search{
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
}
header .header_inner .search #searchbox{
    width: 400px;
    height: 48px;
    border: 1px solid #5f0080;
    box-sizing: border-box;
    outline: none;
    border-radius: 5px;
}
header .header_inner .search #searchbox::placeholder{
    text-indent: 10px;
}
header .header_inner .search label{
    width: 30px;
    height: 30px;
    position: absolute;
    top: 40%;
    right: 10px;
    transform: translateY(-50%);
}
header .header_inner .top_right_nav{
    position: absolute;
    right: 0;
    top: 5px;
}
header .header_inner .top_right_nav ul{
    display: flex;
    font-weight: 600;
    cursor: pointer;
}
header .header_inner .top_right_nav ul li:first-child{
    color: #5f0080;
}
header .header_inner .top_right_nav ul li::after{
    content: "|";
    padding: 0px 5px;
    font-size: 12px;
    color: #333;
}
header .header_inner .top_right_nav ul li:last-child::after{
    display: none;
}
header .header_inner .icon_nav{
    position: absolute;
    width: fit-content;
    right: 0px;
    top: 70%;
    transform: translateY(-50%);
}
header .header_inner .icon_nav ul{
    display: flex;
    cursor: pointer;
}
header .header_inner .icon_nav ul li{
    padding: 0px 20px 0px 0px;
}
header .header_inner .icon_nav ul li:last-child{
    padding: 0px;
}
header .main_nav{
    width: 100%;
    height: 56px;
}
header .main_nav ul{
    width: 1050px;
    height: 100%;
    margin: 0px auto;
    display: flex;
    font-size: 16px;
    position: relative;
    text-align: center;
    align-items: center;
    justify-content:space-between;
    font-weight: 700;
}
header .main_nav li{
    display: block;
    cursor: pointer;
}
header .main_nav li:first-child{
    margin-right: 100px;
}

header .main_nav li:last-child{
    margin-left: 100px;
}
header .main_nav li button{
    padding: 4px 12px;
    border: 1px solid #ccc;
    outline: none;
    border-radius: 20px;
    font-size: 13px;
    cursor: pointer;
    background-color: #fff;
}
header li button .text_purple{
    color: #5f0080;
    font-weight: bold;
}

/* main */
main{
    width: 100%;
}
main .main_inner{
    width: 1050px;
    padding: 30px 0px;
    margin: 0px auto;
    text-align: center;
}
main .main_inner>h2{
    font-size: 28px;
    margin: 0px 0px 20px 0px;
}
main .main_inner>img{
    width: 100%;
    margin-bottom: 50px;
    cursor: pointer;
}
main .main_inner ul{
    display: flex;
    text-align: left;
    margin-bottom: 50px;
}
main .main_inner ul li{
    width: 249px;
    margin-right: 18px;
    font-size: 16px;
    font-weight: 500;
}
main .main_inner ul li:last-child{
    margin: 0px;
}
main .main_inner ul li img{
    width: 100%;
    cursor: pointer;
}
.main_inner ul li img:hover{
    filter: blur(2px);
}
main .main_inner ul li p:last-child{
    font-weight: 600;
}
main .main_inner ul li p:last-child span{
    color: #ff0000;
    font-weight: 800;
}
.product{
    width: 1050px;
    padding: 30px 0px;
    margin: 50px auto;
}
.product h2{
    text-align: center;
    padding-bottom: 20px;
}
.product ul{
    width: 1050px;
    display: flex;
    flex-wrap: wrap;
}
.product ul li{
    width: 249px;
    margin-right: 18px;
    margin-bottom: 30px;
    cursor: pointer;
}
.product ul li:nth-child(4){
    margin: 0px;
}
.product ul li:last-child{
    margin: 0px;
}
.product ul li p{
    font-weight: 500;
    font-size: 16px;
}
.product ul li p:last-child{
    font-weight: 700;
}
.product ul li img{
    width: 249px;
}
.notfound{
    width: 1050px;
    margin: 0px auto;
}

.slide-wrapper{
    width: 1900px;
    height: 370px;
    margin: 0px auto;
    position: relative;
    overflow: hidden;
}
.slide{
    width: 9500px;
    height: 370px;
    display: flex;
    position: absolute;
    left: 0px;
    top: 0px;
    transition: .3s;
}
.slide>li{width: 1900px;}
.slide>li>img{width: 100%;}

.slide-wrapper .button-group>input[type="button"]{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 2px 8px;
}
.slide-wrapper .button-group > .prev{left: 0;}
.slide-wrapper .button-group > .next{right: 0;}
.top_btn{
    padding: 5px 10px;
    background-color: #5f0080;
    color: #fff;
    position: fixed;
    right: 30px;
    bottom:30px;
}
/* footer */
footer{
    width: 100%;
    height: 200px;
    padding-top: 30px;
    border-top: 1px solid #f7f7f7;
}
footer .footer_inner{
    width: 1050px;
    height: 200px;
    margin: 0px auto;
    padding-top: 30px;
    position: relative;
}
footer .footer_inner .customer_center h2{
    font-size: 20px;
    padding-bottom: 17px;
}
footer .footer_inner .customer_center .phone_num{
    font-size: 28px;
    display: inline-block;
    font-weight: 700;
    padding-right: 8px;
}
footer .footer_inner .customer_center span{
    font-weight: 600;
    font-size: 16px;
}
footer .footer_inner>.footer_bottom{
    font-style: normal;
    text-transform: uppercase;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}
footer .footer_inner .footer_nav{
    display: flex;
    position: absolute;
    right: 0;
    top: 30px;
}
footer .footer_inner .footer_nav li{
    padding-right: 20px;
}
footer .footer_inner .footer_nav li:last-child{
    padding: 0px;
}
footer .footer_inner .company_adress{
    position: absolute;
    top: 80px;
    right: 48px;
    font-style: normal;
}