
ul,li{
    list-style: none;
}
a{
    text-decoration: none;
    color: #333;
}
.wrap{
    width: 100%;
    /* position: absolute;
    top: 50%;
    transform: translateY(-50%); */
}
article{
    margin: 100px 0px 100px 0px;
}
fieldset{
    border: none;
    text-align: center;
    position: relative;
    width: 350px;
    left: 50%;
    transform: translateX(-50%);
}
fieldset legend{
    font-weight: 900;
    font-size: 20px;
    margin-bottom: 15px;
    user-select: none;
}
fieldset label{
    display: none;
}
fieldset input{
    width: 340px;
    height: 54px;
    border: 1px solid #ccc;
    outline: none;
    display: block;
    margin: 0px auto 10px auto;
    box-sizing: border-box;
    font-size: 14px;
}
fieldset input::placeholder{
    text-indent: 20px;
    color: #808080;
    font-weight: 700;
}
fieldset .find{
    display: flex;
    position: absolute;
    top: 120px;
    right: 20px;
}
fieldset .find li{
    font-size: 12px;
}
fieldset .find li::after{
    content: "|";
    padding: 0px 5px 0px 5px;
}
fieldset .find li:last-child::after{
    display: none;
    padding: 0px;
}
fieldset .button{
    padding-top: 8%;
}
fieldset #login{
    background-color: #5f0080;
    color: #fff;
    font-size: 16px;
    font-weight: 900;
    border: none;
    cursor: pointer;
}
fieldset #join{
    font-size: 16px !important;
    font-weight: 900;
    color: #5f0080;
    background-color: #fff;
    border: 1px solid #5f0080;
    width: 340px;
    height: 54px;
    margin: 0px auto;
    display: block;
    box-sizing: border-box;
    font-size: 14px;
    text-align: center;
    line-height: 54px;
}

.idCheckbox{
    width: fit-content;
    margin: 20px auto 0px auto;
}
.idCheckbox strong{
    color: #5f0080;
    width: 100%;
}

.pwCheckbox{
    width: fit-content;
    max-width: 100%;
    margin: 20px auto 0px auto;
}
.pwCheckbox strong{
    color: #5f0080;
}